  font-family: "Inter", "Verdana", sans-serif;
  font-size: larger;
  font-weight: 500;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .contacts .contacts__phone:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.contacts .contacts__phone.focus-visible:focus {
  opacity: 0.8;
}
@media (max-width: 1279px) {
  .contacts .contacts__phone {
    margin-right: -10px;
  }
}
@media (max-width: 767px) {
  .contacts .contacts__phone {
    margin: 0;
    color: #000000;
  }
}

.footer {
  position: relative;
  margin-top: auto;
  padding: 80px 0;
  background-color: #b49d46;
  background-image: url("../img/footer.jpg");
  background-size: cover;
  background-position: 50% 50%;
}
@media (max-width: 767px) {
  .footer {
    padding: 40px 0;
  }
}
.footer__list {
  position: relative;
  display: flex;
  z-index: 2;
}
@media (max-width: 767px) {
  .footer__list {
    flex-direction: column;
  }
}
.footer__item {
  padding: 4px 20px;
  border-right: 1px solid #fff;
}
.footer__item:first-child {
  padding-left: 0;
}
.footer__item:last-child {
  border: none;
}
@media (max-width: 767px) {
  .footer__item {
    border: none;
    padding-left: 0;
  }
}
.footer__link {
  color: #ffffff;
  font-family: "Poppins", "Arial", sans-serif;
  text-transform: uppercase;
  transition: opacity 0.3s ease;
}
@media (hover: hover) {
  .footer__link:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.footer__link.focus-visible:focus {
  opacity: 0.8;
}

@keyframes fadeInFromNone {
  0% {
    display: none;
    opacity: 0;
  }
  1% {
    display: block;
    opacity: 0;
  }
  100% {
    display: block;
    opacity: 1;
  }
}
.button-up {
  position: fixed;
  bottom: 40px;
  right: 20px;
  width: 54px;
  height: 55px;
  background-color: rgba(180, 157, 70, 0.6);
  border: none;
  opacity: 1;
  transition: background-color 0.6s ease;
  z-index: 1000;
  cursor: pointer;
  animation: fadeInFromNone 0.5s ease-in-out;
}
@media (max-width: 767px) {
  .button-up {
    width: 44px;
    height: 46px;
    right: 15px;
    bottom: 30px;
  }
}
.button-up svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -59%);
  color: #ffffff;
}
@media (max-width: 767px) {
  .button-up svg {
    width: 24px;
    height: 24px;
  }
}
.button-up:focus {
  background-color: #b39e47;
}
.button-up:hover {
  background-color: rgba(180, 157, 70, 0.5);
}
.button-up:active {
  background-color: #b39e47;
}

.button-up.is-hidden {
  display: none;
  opacity: 0;
}

.anim-bottom-hidden {
  opacity: 0;
  transform: translateY(15vh);
  transition: opacity 0.6s ease, transform 0.7s ease;
}

.anim-right-hidden {
  opacity: 0;
  transform: translateX(10vw);
  transition: opacity 0.8s ease, transform 0.8s ease;
}

.anim-left-hidden {
  opacity: 0;
  transform: translateX(-10vw);
  transition: opacity 0.8s ease, transform 0.8s ease;
}

.anim-hidden {
  opacity: 0;
  transition: opacity 1s ease;
}

.anim-visible {
  opacity: 1;
  transform: translate(0);
}

